<template>
  <div class="map-container" ref="container">
    <section class="sectionMap">
        <div class="mapBox" id="mapBox"></div>
        <div class="mapInfo">
              <h3 class="titleArea">Grand Canyon Palace</h3>
              <p class="stitleAreat itleAreaStreet">25 E Odgen Avenue</p>
              <p class="stitleAreat titleAreaCity">Las Vegas. NV</p>
              <p class="stitleAreat titleCountry">United States</p>
        </div>
    </section>
  </div>
</template>



<script scoped>
  import map from './js/map.js';
  import './stylus/TrafficControl_min.css';
  export default map;
</script>

<!-- Add "scoped" attribute to limit CSS to this component only @import './login.styl' -->
<style scoped lang="stylus">
    @import './stylus/map.styl'
</style>


